.form {
  margin: 50px auto 100px;
  display: flex;
  flex-direction: column;
  max-width: 500px;
}

@media screen and (max-width: 700px) {
  .form {
    margin-bottom: 50px;
    max-width: 450px;
  }
}

@media screen and (max-width: 550px) {
  .form {
    max-width: 300px;
  }
}
